body {
  margin: 0;
  padding: 0;
  background-image: url(/swapibox/static/media/space.0c79c07e.jpg);
  background-size: cover;
  background-color: hsla(0, 0%, 0%, 0.75);
  background-blend-mode: multiply;
  background-repeat: no-repeat;
}

.ScrollingText {
  grid-column: 1 / 2;
  grid-row: 1 / 3;
  color: gold;
  font-size: 2.5rem;
  text-align: justify;
  overflow: hidden;
  -webkit-perspective: 400px;
          perspective: 400px;
  -webkit-perspective-origin: 50% 30%;
          perspective-origin: 50% 30%;
}

.title {
  line-height: 1;
  margin-bottom: 0;
  text-align: left;
}

.release-date {
  margin-top: 0.5rem;
}

.scrolling-wrapper {
  -webkit-animation: crawl 40s linear infinite;
          animation: crawl 40s linear infinite;
  margin: 0 auto;
  position: relative;
  top: -80vh;
  -webkit-transform: rotateX(55deg) translate3d(0, 885px, 325px);
          transform: rotateX(55deg) translate3d(0, 885px, 325px);
  width: 80%;
  z-index: 5;
}

.fade {
  position: relative;
  height: 80vh;
  width: 100%;
  background: linear-gradient(black, rgba(0, 0, 0, 0.8), transparent, transparent, transparent, transparent);
  z-index: 10;
}

@-webkit-keyframes crawl {
  from {
    -webkit-transform: rotateX(55deg) translate3d(0, 885px, 325px);
            transform: rotateX(55deg) translate3d(0, 885px, 325px);
  }
  to {
    -webkit-transform: rotateX(55deg) translate3d(0, -3000px, 325px);
            transform: rotateX(55deg) translate3d(0, -3000px, 325px);
  }
}

@keyframes crawl {
  from {
    -webkit-transform: rotateX(55deg) translate3d(0, 885px, 325px);
            transform: rotateX(55deg) translate3d(0, 885px, 325px);
  }
  to {
    -webkit-transform: rotateX(55deg) translate3d(0, -3000px, 325px);
            transform: rotateX(55deg) translate3d(0, -3000px, 325px);
  }
}
@font-face {
  font-family: "StarJedi";
  src: url(/swapibox/static/media/Starjedi.cc3019aa.ttf) format("truetype");
  font-weight: normal;
  font-style:normal;
}

.Header {
  font-family: 'News Cycle', sans-serif;
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.upper-header {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-title {
  font-family: StarJedi, 'News Cycle', sans-serif;
  text-align: center;
  color: gold;
  margin: 0 0 5px 150px;
  font-size: 2.25rem;
  line-height: 4rem;
  letter-spacing: 8px;
  background-color: transparent;
  text-decoration: none;
}

.header-title:hover {
  color: gold;
}

nav {
  width: 70%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.display-none {
  display: none;
}

.Button {
  font-family: 'News Cycle', sans-serif;
  height: 45px;
  width: 150px;
  border: 2px solid gold;
  background-color: transparent;
  color: gold;
  font-size: 1.25rem;
  border-radius: 15px;
  padding-bottom: 3px;
  letter-spacing: 1px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Button:hover {
  color: white;
  border-color: white;
}

.favorites {
  height: 75px;
  max-width: 100px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 7px;
}

.favorites:hover {
  box-shadow: 0 0 15px gold, 0 0 15px white;
}

.favorites-badge {
  font-size: 1.75rem;
  font-weight: 700;
  margin: 0;
  line-height: 1;
}

.people:hover {
  box-shadow: 0 0 15px deepskyblue, 0 0 15px white;
}

.planets:hover {
  box-shadow: 0 0 15px springgreen, 0 0 15px white;
}

.vehicles:hover {
  box-shadow: 0 0 15px red, 0 0 15px white;
}

.active {
  background-color: gold;
  color: black;
  font-weight: 700;
}

.active:hover {
  color: black;
  border-color: black;
}

.active:focus {
  outline: none;
}
.Card {
  background-color: #444;
  border-radius: 25px;
  box-shadow: 0 0 0 0 transparent;
  display: grid;
  font-weight: 700;
  grid-template: 'heading hilt' 50px
                 'content content' 1fr
                  / 1fr 90px;
  letter-spacing: 1px;
  margin: 25px;
  width: 300px;
  height: 360px;
  z-index: 0;
}

.saber {
  margin-top: 27px;
  border-radius: 5px;
  grid-area: heading;
  height: 10px;
  justify-self: end;
  z-index: 3;
}

.hilt {
  grid-area: hilt;
  margin-left: 1px;
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
  z-index: 6;
}

.hilt:hover {
  cursor: pointer;
}

.name {
  grid-area: heading;
  margin: 15px 0px 0px 20px;
  text-align: center;
  z-index: 9;
}

.content {
  grid-area: content;
  color: white;
  list-style: none;
  margin: 20px 50px 25px 50px;
  padding: 0;
}

.details {
  font-weight: 400;
}

.people-is-glowing {
  box-shadow: 0 0 10px deepskyblue, 0 0 15px white;
  background-color: hsl(195, 85%, 70%);
  transition: width 1s;
  width: 185px;
}

.planets-is-glowing {
  box-shadow: 0 0 10px springgreen, 0 0 15px white;
  background-color: hsl(150, 100%, 70%);
  transition: width 1s;
  width: 185px;
}

.vehicles-is-glowing {
  box-shadow: 0 0 10px red, 0 0 15px white;
  background-color: hsl(0, 100%, 62%);
  transition: width 1s;
  width: 185px;
}

.favorites-is-glowing {
  box-shadow: 0 0 10px gold, 0 0 15px white;
  background-color: hsl(51, 100%, 50%);
  transition: width 1s;
  width: 185px;
}

.people-is-off,
.planets-is-off,
.vehicles-is-off,
.favorites-is-off {
  background-color: transparent;
  box-shadow: 0 0 0  transparent;
  transition: background-color 0s 1.8s , box-shadow 0s 1.8s, width 1s 0.8s;
  width: 1px;
}

.people-bg-glow {
  box-shadow: 0 0 50px -5px deepskyblue, 0 0 60px -5px white;
  transition: box-shadow 1s 0.7s;
}

.planets-bg-glow {
  box-shadow: 0 0 50px -5px springgreen, 0 0 60px -5px white;
  transition: box-shadow 1s 0.7s;
}

.vehicles-bg-glow {
  box-shadow: 0 0 50px -5px red, 0 0 60px -5px white;
  transition: box-shadow 1s 0.7s;
}

.favorites-bg-glow {
  box-shadow: 0 0 50px -5px gold, 0 0 60px -5px white;
  transition: box-shadow 1s 0.7s;
}

.people-bg-off,
.planets-bg-off,
.vehicles-bg-off,
.favorites-bg-off {
  box-shadow: 0 0 0 0 transparent;
  transition: box-shadow 1s;
}

.is-black {
  color: black;
  transition: color 1s;
}

.is-white {
  color: white;
  transition: color 1s 0.8s;
}
.CardContainer {
  grid-row: 2 / 3;
  grid-column: 2 / 3;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: scroll;
}

.default,
.favorites-default {
  color: gold;
  text-align: center;
  font-size: 2rem;
  margin-bottom: 0;
}

.favorites-default-text {
  color: gold;
  font-size: 1.75rem;
  margin-top: 0;
  align-self: flex-start;
  text-align: center;
}

.display-none {
  display: none;
}

.people-loading {
  background-image: url(/swapibox/static/media/skywalker-fett.c9b2812d.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.planets-loading {
  background-image: url(/swapibox/static/media/hoth.260ea6fb.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.vehicles-loading {
  background-image: url(/swapibox/static/media/landspeeder.9445723c.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
.App {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 33% 1fr;
  grid-template-rows: 35% 1fr;
  grid-row-gap: 40px;
  font-family: 'News Cycle', sans-serif;
  font-weight: 400;
}

.loading {
  height: 100vh;
  width: 100vw;
  background-image: url(/swapibox/static/media/bb8-loading.c8a19f31.gif);
  background-position: center;
  background-repeat: no-repeat;
  background-color: rgb(8, 31, 41)
}

h1,
h2,
h3,
h4 {
  font-weight: 700;
}

.display-none {
  display: none;
}
